import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AllUsersComponent } from './all-users/all-users.component';
import { SuperUserComponent } from './super-user.component';
import { PendingContactComponent } from '../edit-contact/pending-contact/pending-contact.component';
import { PendingBankComponent } from '../edit-bankdetails/pending-bank/pending-bank.component';
import { AuthGuard } from '../auth/auth.guard';


const routes: Routes = [
    {path:'all-users',component: AllUsersComponent,canActivate:[AuthGuard]},
    {path:'all-applications',component: SuperUserComponent,canActivate:[AuthGuard]},
    {path:'pending-contactslist',component: PendingContactComponent,canActivate:[AuthGuard]},
    {path:'pending-banklist',component: PendingBankComponent,canActivate:[AuthGuard]},
    
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SuperUserRouting {}
